header{
    height:70px;
    background-color:#b0bfc5;
    display: flex;
    justify-content: center;
    align-items: center;
}
header p{
    font-size: 27px;
    font-family: 'Montserrat', sans-serif;
}
main{
    height: 440px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #DCE1E3;
}
.parent-box{
    display: grid;
    height: 20rem;
    grid-template-columns: repeat(3,9rem);
}
.row{
    display: flex;
    height: 33.33%;
}
.box{
    border: 3.3px solid;
    border-color: #510f0f;
    font-size: 6rem;
    display: flex;
    height: 8rem;
    width: 8.8rem;
    align-items: center;
    justify-content: center;
    margin: -2px;
    font-family: 'Montserrat', sans-serif;
}

.x{
    color: rgb(255, 0, 0);
}
.o{
    color: rgb(8, 0, 255);
}

@media screen and (max-width:1024px) {
    .box {
        height: 7rem;
        width: 7.8rem;
    }
    .parent-box{
        grid-template-columns: repeat(3,8rem);

    }
}

@media screen and (max-width:768px) {
    .box {
        height: 6rem;
        width: 6.8rem;
    }
    .parent-box{
        height: 18rem;

        grid-template-columns: repeat(3,7rem);

    }
}

@media screen and (max-width:425px) {
    .box {
        height: 4rem;
        width: 4.8rem;
    }
    .parent-box{
        height: 12rem;
        grid-template-columns: repeat(3,5rem);
    

    }
}
